.Icon{position:relative;margin:0;width:35px}.Icon img{width:100%}

.start-menu{display:flex;width:100px;padding:0 5px;text-align:center;outline:none}.start-menu .Icon{margin:0}.start-menu .Icon img{margin-top:3px;height:25px}.start-menu *{pointer-events:none}.start-menu.in-border-hard div{transform:translateY(2px)}

.applications{display:flex;width:100%;min-width:100px}.applications .btn{overflow:hidden;width:100%;max-width:260px;padding-left:10px;text-overflow:ellipsis}

.time{width:160px;min-width:100px;font-size:18pt;text-align:center}

.hero-foot{display:flex;z-index:999;padding:7px 0}.hero-foot div{height:30px;margin:0 5px}

.menu{z-index:999;position:absolute;bottom:52px;left:0;display:flex;width:270px;margin:5px}.menu .menu-list{width:100%}.menu .menu-list .item{display:flex;width:100%;padding:5px}.menu .menu-list .item .text{padding:6px}.menu .menu-list .item .underline{border-bottom:1px solid black}.menu .menu-list .item .Icon{margin:0 10px}.menu .menu-list :hover{color:white;background-color:#030084}.menu .menu-list :hover .underline{border-color:white}.menu .menu-list .item:last-child{border-top:2px solid #e3e3e3}.menu .sidebar{position:relative;width:50px;background-color:#808181}.menu .sidebar .content{position:absolute;bottom:75px;left:-78px;transform:rotate(-90deg);color:#b1b1b1;font-size:40px}

.window{position:absolute;left:0;right:0;top:50px;margin:0 auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content;overflow:auto}.window .header{padding:2px 5px;display:flex;font-size:15px;pointer-events:none;background-color:#808080}.window .header.top{background-color:#000080;color:white}.window .title{padding:20px;margin:0 !important;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;color:black !important}.window .main-content{max-width:600px;width:600px;padding:25px;margin:unset}.window .main-content .main{display:flex}.window .main-content .main .left,.window .main-content .main .right{padding:10px}.window .main-content .main .left{width:70%}.window .main-content .main .right{width:30%}.closeBtn{pointer-events:all;position:absolute;right:3px;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding:3px;height:15px;line-height:5px;margin:3px}

*{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}html{overflow:hidden}ul{margin-left:35px}li{list-style-type:decimal;padding:10px}.hero.is-success{overflow:hidden;background-color:#008083}.default{color:black;background-color:#c0c0c0;font-family:monospace;font-size:15pt;-webkit-font-smoothing:none}.yellow{background-color:#fcfce2}.pop-border-main{box-shadow:-1px -1px 0px 1px #fffffe,-3px -3px 0px 1px #abc9cb,1px -1px 0px 1px #7e7e7c,-1px 1px 0px 1px #7e7e7c,1px 1px 0px 1px #7e7e7c,-3px 1px 0px 1px #abc9cb,1px -3px 0px 1px #abc9cb,2px 2px 0px 1px #010303,0px 0px 0px 4px #010303,0 0 7px rgba(0,0,0,0.9)}.pop-border{box-shadow:1px 1px 0px 1px #7e7e7c, 3px 3px 0px 1px #010303, -1px 1px 0px 1px #e6e6e6, -1px 3px 0px 1px #010303, 1px -1px 0px 1px #e6e6e6, 3px -1px 0px 1px #010303, -1px -1px 0px 1px #e6e6e6}.in-border-hard{background-color:#fff;background-image:linear-gradient(45deg, #b3b3b3 25%, transparent 25%, transparent 75%, #b3b3b3 75%, #b3b3b3),linear-gradient(45deg, #b3b3b3 25%, transparent 25%, transparent 75%, #b3b3b3 75%, #b3b3b3);background-position:0 0, 25px 25px;background-size:2px 2px;box-shadow:1px 1px 0px 1px #d7d7d6, 3px 3px 0px 1px #ececec, -1px 1px 0px 1px #5a5a59, -1px 3px 0px 1px #ececec, 1px -1px 0px 1px #5a5a59, 3px -1px 0px 1px #ececec, -1px -1px 0px 1px #5a5a59}.in-border-hard.start-menu{background:unset}.in-border-soft{box-shadow:-1px -1px 0px 1px #9f9ca0, 1px 1px 0px 1px #dddddd}.hidden{display:none}

